  .infra-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Ajuste automático */
    gap: 25px;
  }
  
  
  .infra-item{
    width: 100%;
    border-radius: 25px;
    background-color: #efefef;
    border: 3px solid #efefef;
    transition: 0.3s;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.20);
  }

  .infra-item img {
    border-radius: 25px 25px 0 0;
    width: 100%; /* Ajusta la imagen al tamaño del contenedor */
    height: auto; /* Mantiene la proporción de la imagen */
  }
  
  .infra-item:hover {
    border: 3px solid #FF8C00;   
  }
  
  .infra-item p {
    color: #1D1D1B;
    font-family: 'D-DIN';
  }

  .infra-container h2{
    color: #1D1D1B;
    font-family: 'D-DIN';
    
  }